import React from 'react';
import {
    Box,
    Button,
    Chip,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    FormControl,
    FormControlLabel,
    Grid,
    Paper,
    Radio,
    RadioGroup,
    Stack,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Tooltip,
    Typography
} from '@mui/material';
import { useEffect } from 'react';
import { useContext } from 'react';
import { AuthContext } from 'src/App';
import axios from 'axios';
import { apiBaseURL } from 'src/config-global';
import { useAuthContext } from 'src/auth/useAuthContext';

const ACCEPTED = 'Accepted';
const REJECTED = 'Rejected';
const NORES = 'No Response';

const TrackOnboarding = () => {
    const { user } = useAuthContext();
    const [radioValue, setRadioValue] = React.useState('Requested');
    const [requests, setRequests] = React.useState([]);
    const [pending, setPending] = React.useState([]);
    const [managerRes, setManagerRes] = React.useState(null);
    const [responses, setResponses] = React.useState(null);

    useEffect(() => {
        if (!user) return;
        axios.get(`${apiBaseURL}/manager/onboarding/${user.company}?key=managerId&value=${user.uid}`).then((res) => {
            setRequests(res.data);
        });
        axios.get(`${apiBaseURL}/manager/onboarding/${user.company}?key=managerEmail&value=${user.email}`).then((res) => {
            setPending(res.data);
        });
    }, [user]);

    useEffect(() => {
        if (!user) return;
        setResponses(() => {
            const temp = {};
            pending.forEach((item, index) => {
                const manager = item.managers[item.managers.findIndex((manager) => manager.managerEmail === user.email)];
                temp[index] = manager.response;
            });
            return temp;
        });
    }, [pending]);

    const [open, setOpen] = React.useState(false);
    const handleClickOpen = (data) => () => {
        setOpen(true);
        setManagerRes(data);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const handleRequest = (response, id, index, ix) => (e) => {
        const payload = {
            id,
            managerEmail: user.email,
            response
        };
        axios.post(`${apiBaseURL}/manager/onboarding/approval`, { payload }).then((res) => {
            setResponses((prev) => ({
                ...prev,
                [index]: res.data.managers[ix].response
            }));
        });
    };

    const descriptionElementRef = React.useRef(null);
    React.useEffect(() => {
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);

    return (
        <>
            <Dialog
                open={open}
                onClose={handleClose}
                scroll="paper"
                aria-labelledby="scroll-dialog-title"
                aria-describedby="scroll-dialog-description"
            >
                {managerRes && (
                    <>
                        <DialogTitle id="scroll-dialog-title">
                            <Stack direction="row" justifyContent="space-between" alignItems="center">
                                <div>Manager Responses</div>
                                <div>
                                    <Typography variant="overline">
                                        &nbsp;&nbsp;&nbsp;By:&nbsp;&nbsp;&nbsp;{managerRes.position.manager}
                                        <br />
                                        For:&nbsp;&nbsp;&nbsp;{`${managerRes.personal.firstName + ' ' + managerRes.personal.lastName}`}
                                    </Typography>
                                </div>
                            </Stack>
                        </DialogTitle>
                        <DialogContent dividers={scroll === 'paper'}>
                            <Box id="scroll-dialog-description" ref={descriptionElementRef} tabIndex={-1}>
                                <TableContainer component={Paper}>
                                    <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                        <TableHead>
                                            <TableRow>
                                                <TableCell align="center">Manager</TableCell>
                                                <TableCell align="center">Response</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {managerRes.managers.map(({ managerEmail: email, response }) => {
                                                return (
                                                    <TableRow key={email}>
                                                        <TableCell align="center" scope="row">
                                                            <Typography variant="h6">{email}</Typography>
                                                        </TableCell>
                                                        <TableCell align="center" variant="h6">
                                                            <Chip
                                                                label={response}
                                                                color={
                                                                    response === NORES
                                                                        ? 'warning'
                                                                        : response === ACCEPTED
                                                                        ? 'success'
                                                                        : 'error'
                                                                }
                                                            />
                                                        </TableCell>
                                                    </TableRow>
                                                );
                                            })}
                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </Box>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose}>Close</Button>
                        </DialogActions>
                    </>
                )}
            </Dialog>

            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                <Grid item xs={12}>
                    <Typography sx={{ float: 'left' }} variant="h3">
                        Track Onboarding Requests
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <FormControl>
                        <RadioGroup
                            row
                            aria-labelledby="demo-row-radio-buttons-group-label"
                            name="row-radio-buttons-group"
                            value={radioValue}
                            onChange={(e) => setRadioValue(e.target.value)}
                        >
                            <FormControlLabel value="Requested" control={<Radio />} label="Requested" />
                            <FormControlLabel value="Pending Actions" control={<Radio />} label="Pending Actions" />
                        </RadioGroup>
                    </FormControl>
                    <br />
                    <br />
                    {radioValue === 'Requested' ? (
                        <Grid item xs={12}>
                            <TableContainer component={Paper}>
                                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell align="center">First Name</TableCell>
                                            <TableCell align="center">Last Name</TableCell>
                                            <TableCell align="center">Job Title</TableCell>
                                            {/* <TableCell align="center">Reporter</TableCell> */}
                                            <TableCell align="center">Start Date</TableCell>
                                            <TableCell align="center">Status</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {requests.map((item, index) => {
                                            return (
                                                <TableRow key={index}>
                                                    <TableCell align="center" scope="row">
                                                        <Typography variant="h6">{item.personal.firstName}</Typography>
                                                    </TableCell>
                                                    <TableCell align="center" variant="h6">
                                                        {item.personal.lastName}
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        <Typography sx={{ pt: 1 }} variant="h6">
                                                            {item.position.empType}
                                                        </Typography>
                                                    </TableCell>
                                                    {/* <TableCell align="center">
                                                    <Typography sx={{ pt: 1 }} variant="h6">
                                                        {item.position.manager}
                                                    </Typography>
                                                </TableCell> */}
                                                    <TableCell align="center">
                                                        <Typography sx={{ pt: 1 }} variant="h6">
                                                            {item.position.start}
                                                        </Typography>
                                                    </TableCell>
                                                    <TableCell align="center">
                                                        <Button
                                                            disabled={!item.managers.length}
                                                            sx={{ mr: 1 }}
                                                            variant="contained"
                                                            color="primary"
                                                            onClick={handleClickOpen(item)}
                                                            >
                                                            {!item.managers?.length ? 'No Delegations' : 'View'}
                                                        </Button>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </Grid>
                    ) : radioValue === 'Pending Actions' ? (
                        <Grid item xs={12}>
                            <TableContainer component={Paper}>
                                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell align="center">First Name</TableCell>
                                            <TableCell align="center">Last Name</TableCell>
                                            <TableCell align="center">Job Title</TableCell>
                                            <TableCell align="center">Reporter</TableCell>
                                            <TableCell align="center">Start Date</TableCell>
                                            <TableCell align="center">Status</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {responses &&
                                            pending.map((item, index) => {
                                                const ix = item.managers.findIndex((manager) => manager.managerEmail === user.email);
                                                const accFlag = responses[index] === ACCEPTED;
                                                const rejFlag = responses[index] === REJECTED;
                                                return (
                                                    <TableRow key={index}>
                                                        <TableCell align="center" scope="row">
                                                            <Typography variant="h6">{item.personal.firstName}</Typography>
                                                        </TableCell>
                                                        <TableCell align="center" variant="h6">
                                                            {item.personal.lastName}
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            <Typography sx={{ pt: 1 }} variant="h6">
                                                                {item.position.empType}
                                                            </Typography>
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            <Typography sx={{ pt: 1 }} variant="h6">
                                                                {item.position.manager}
                                                            </Typography>
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            <Typography sx={{ pt: 1 }} variant="h6">
                                                                {item.position.start}
                                                            </Typography>
                                                        </TableCell>
                                                        <TableCell align="center">
                                                            <Button
                                                                // disabled={accFlag}
                                                                sx={{ mr: 1, width: '70px' }}
                                                                variant={accFlag ? 'contained' : 'outlined'}
                                                                color={accFlag ? 'success' : 'secondary'}
                                                                onClick={
                                                                    accFlag ? () => null : handleRequest(ACCEPTED, item._id, index, ix)
                                                                }
                                                            >
                                                                {accFlag ? 'Accepted' : 'Accept'}
                                                            </Button>
                                                            <Button
                                                                // disabled={rejFlag}
                                                                sx={{ width: '70px' }}
                                                                variant={rejFlag ? 'contained' : 'outlined'}
                                                                color={rejFlag ? 'error' : 'secondary'}
                                                                onClick={
                                                                    rejFlag ? () => null : handleRequest(REJECTED, item._id, index, ix)
                                                                }
                                                            >
                                                                {rejFlag ? 'Rejected' : 'Reject'}
                                                            </Button>
                                                        </TableCell>
                                                    </TableRow>
                                                );
                                            })}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </Grid>
                    ) : null}
                </Grid>
            </Grid>
        </>
    );
};

export default TrackOnboarding;
